import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Card, Statistic, Row, Col, Spin, Typography } from 'antd';
import { Chart, CategoryScale, BarElement, LinearScale, Title, Tooltip, Legend, TimeScale, PointElement } from 'chart.js';
import { Bar } from 'react-chartjs-2';
import 'antd/dist/reset.css';
import './App.css';

Chart.register(CategoryScale, BarElement, LinearScale, Title, Tooltip, Legend, TimeScale, PointElement);

function App() {
  const [serverData, setServerData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [historicalData, setHistoricalData] = useState([]);

  useEffect(() => {
    fetchServerStatus();
    fetchHistoricalData();

    // 30   
    const interval = setInterval(() => {
      fetchServerStatus();
      fetchHistoricalData();
    }, 30000);

    return () => clearInterval(interval);
  }, []);

  const fetchServerStatus = async () => {
    setLoading(true);
    try {
      const response = await axios.get('/status');
      setServerData(response.data);
    } catch (error) {
      console.error('Error fetching server status:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchHistoricalData = async () => {
    try {
      const response = await axios.get('/historical'); //   24    
      setHistoricalData(response.data);
    } catch (error) {
      console.error('Error fetching historical data:', error);
    }
  };

  if (loading) {
    return (
      <div className="loading-container">
        <Spin size="large" />
      </div>
    );
  }

  //  15  
  const aggregatedData = Array.from({ length: 96 }, (_, index) => {
    const start = new Date();
    start.setMinutes(start.getMinutes() - 1440 + index * 15, 0, 0); // 24  15 
    const end = new Date(start);
    end.setMinutes(start.getMinutes() + 15);

    const dataInRange = historicalData.filter(dataPoint => {
      const timestamp = new Date(dataPoint.timestamp);
      return timestamp >= start && timestamp < end;
    });

    const online = dataInRange.some(dataPoint => dataPoint.online);
    return {
      timestamp: start,
      online: online ? 1 : 0,
    };
  });

  const aggregatedLabels = aggregatedData.map((data) => new Date(data.timestamp).toLocaleString('ko-KR', { year: '2-digit', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit' }));
  const aggregatedOnlineStatus = aggregatedData.map((data) => data.online === 1 ? 1 : 0);
  const aggregatedOfflineStatus = aggregatedData.map((data) => data.online === 0 ? 1 : 0);

  const historicalChartData = {
    labels: aggregatedLabels,
    datasets: [
      {
        label: 'Online',
        data: aggregatedOnlineStatus,
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
        barThickness: 'flex',
        maxBarThickness: 30,
        categoryPercentage: 1,
        barPercentage: 0.6,
      },
      {
        label: 'Offline',
        data: aggregatedOfflineStatus,
        backgroundColor: 'rgba(255, 99, 132, 0.6)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1,
        barThickness: 'flex',
        maxBarThickness: 30,
        categoryPercentage: 1,
        barPercentage: 0.6,
      },
    ],
  };

  return (
    <div className="App">
      <Typography.Title level={2} style={{ textAlign: 'center', margin: '20px 0' }}>
           
      </Typography.Title>
      {serverData.online ? (
        <div>
          <Row gutter={[16, 16]} justify="center">
            <Col span={6}>
              <Card>
                <Statistic title=" " value="" valueStyle={{ color: '#3f8600' }} />
              </Card>
            </Col>
            <Col span={6}>
              <Card>
                <Statistic title="" value={serverData.version} />
              </Card>
            </Col>
            <Col span={6}>
              <Card>
                <Statistic title="" value={`${Math.round(serverData.ping)} ms`} />
              </Card>
            </Col>
          </Row>
          <Row gutter={[16, 16]} justify="center" style={{ marginTop: '20px' }}>
            <Col span={18}>
              <Card title="24  ">
                <div className="bar-container">
                  <Bar 
                    data={historicalChartData} 
                    options={{ 
                      responsive: true, 
                      maintainAspectRatio: false, 
                      scales: { 
                        y: { 
                          display: false, 
                        },
                        x: {
                          display: false,
                          stacked: true,
                        }
                      },
                      plugins: {
                        legend: {
                          display: true,
                          position: 'top',
                        },
                        tooltip: {
                          enabled: true,
                          callbacks: {
                            label: function(context) {
                              return context.dataset.label + ': ' + (context.raw === 1 ? 'Online' : 'Offline');
                            }
                          }
                        },
                      },
                      interaction: {
                        mode: 'index',
                        intersect: false,
                      },
                    }} 
                  />
                </div>
              </Card>
            </Col>
          </Row>
          <Row gutter={[16, 16]} justify="center" style={{ marginTop: '20px' }}>
            <Col span={18}>
              <Card title=" ">
                {serverData.playersList.length > 0 ? (
                  <ul>
                    {serverData.playersList.map((player, index) => (
                      <li key={index}>{player}</li>
                    ))}
                  </ul>
                ) : (
                  <p>    .</p>
                )}
              </Card>
            </Col>
          </Row>
        </div>
      ) : (
        <div style={{ textAlign: 'center', marginTop: '50px' }}>
          <Typography.Title level={3} style={{ color: 'red' }}>
             
          </Typography.Title>
          <p> : {serverData.error}</p>
        </div>
      )}
    </div>
  );
}

export default App;
